@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::selection {
  color: #ffffff !important;
  background-color: #9147FF !important;
}

body {
  background-color: #f7f7f8 !important;
}

header {
  font-family: 'Noto Sans KR', sans-serif !important;
}

main {
  font-family: 'Noto Sans KR', sans-serif !important;
  display: flex !important;
  flex-flow: column !important;
}

a {
  cursor: pointer;
}

.dropdown-menu {
  margin: 0.2rem !important;
  min-width: 200px !important;
  border: 2px solid #9147FF !important;
  border-radius: 5px !important;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.2) !important;
}

.dropdown-item {
  height: 40px !important;
  padding: 8px 24px !important;
  font-size: 12pt !important;
  cursor: pointer !important;
}

.dropdown-item:hover {
  background-color: lightgray !important;
}

.dropdown-item:active {
  background-color: gray !important;
}

.dropdown-divider {
  margin: 10px 0 !important;
}
.NavBar .Label {
  display: inline-block;
}

.navbar {
  height: 60px;
  padding: 0;
  font-size: 0.875rem;
  background-color: white;
  box-shadow: 0 0.1px 0.1rem 0.1rem rgba(58, 59, 69, 0.15);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar-main {
  z-index: 10000;
}

.navbar-brand {
  display: flex;
  height: 100%;
  margin: 0;
  padding: 14px 20px;
  background-color: gray;
  color: white !important;
  cursor: pointer;
}

.nav-icon {
  width: 32px;
  height: 32px;
  margin-right: 10px;
}

.navbar-toggler {
  margin-right: 10px;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
}

.navbar-toggler:hover {
  background-color: gray;
}

.navbar-collapse {
  background-color: white;
}

.nav-item:hover {
  background-color: lightgray;
}

.nav-link {
  width: 100%;
  height: 100%;
  padding: 0 20px !important;
  line-height: 60px;
  cursor: pointer;
}

.dropdown-toggle::after {
  display: none;
}

.nav-bar-area {
  height: 60px;
}
.IconBig {
  margin: auto 0;
  width: 48px;
  height: 48px;
}

.Icon {
  margin: auto 0;
  width: 32px;
  height: 32px;
}

.IconSmall {
  margin: auto 0;
  width: 24px;
  height: 24px;
}

.IconTiny {
  margin: auto 0;
  width: 16px;
  height: 16px;
}

.IconFit {
  vertical-align: unset;
  height: 100%;
  padding: 4px;
}
.ImageLabel {
    vertical-align: middle;
}
.FooterArea {
  display: flex;
  height: 50px;
  padding: 15px;
  font-family: "Nanum Gothic", sans-serif !important;
  font-size: 12px;
  background-color: white;
  box-shadow: 0 0.1px 0.1rem 0.1rem rgba(58, 59, 69, 0.15);
}

.FooterArea .contact {
  display: flex;
  margin-left: auto;
}

@media (max-width: 600px) {
  .FooterArea .contact {
      display: none;
  }
}
.Loading {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}
.Label {
    display: block;
    margin: auto 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 5px;
    font-size: 17px;
    white-space: pre-wrap;
}
.ErrorBoundary {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}
.Button {
	display: block;
    margin: 0 5px;
    padding: 7px 12px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #eff0f2;
    border: none;
    border-radius: 5px;
    font-size: 15px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	outline: none;
	cursor: pointer;
}

.Button:hover {
	background-color: #dadada;
}

.Button:active {
	background-color: #b6b6b6;
}
.LinkButton {
	display: block;
    margin: 0 5px;
    padding: 7px 12px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #000000;
    background-color: #eff0f2;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.LinkButton:hover {
    background-color: #dadada;
}

.LinkButton:active {
	background-color: #b6b6b6;
}
.ButtonArea {
    display: flex;
    padding: 10px;
    flex-flow: row wrap;
}
